import { useParams } from 'react-router-dom';
import { Container, Content, Description, Details, Icons, ImageContainer, ImagesWrapper, Section, UserImg } from './style';
import { useEffect, useState } from 'react';
import { Button, Input } from '../Generic'
import { Checkbox } from 'antd';
import noimg from "../../assets/img/noimg.png"
import Map from '../Generic/Map';
import Recent from '../Recent'
import { useMutation } from '@tanstack/react-query';

function HouseItem() {
    const [data, setData] = useState({})
    const params = useParams()
    const [isFavourite, setIsFavourite] = useState(data?.favourite);

    const mutation = useMutation({
        mutationFn: async (id) => {
            const res = await fetch(`https://housing-backend-production.up.railway.app/api/houses/addFavourite/${id}?favourite=${!data?.favourite}`, {
                method: "PUT",
            });   

            if (!res.ok) {
                throw new Error("Failed to update favourite status");
            }

            return res.json();
        },
    });

    const handleFavourite = (e) => {
        mutation.mutate(data?._id)
        const prevState = isFavourite;
        setIsFavourite((prev) => !prev);

        mutation.mutate(data?._id, {
            onError: () => {
                setIsFavourite(prevState);
            }
        });
    }

    useEffect(() => {
        if (params?.id) {
            fetch(`https://housing-backend-production.up.railway.app/api/houses/${params.id}`)
                .then(res => {
                    if (!res.ok) {
                        throw new Error(`HTTP error! Status: ${res.status}`);
                    }
                    return res.json();
                })
                .then(res => {
                    setData(res || {});
                    window.scrollTo(0, 0);
                })
                .catch(error => {
                    console.error('Fetch xatosi:', error);
                });
        }
    }, [params.id]);

    return (
        <>
            <Container>
                <ImageContainer>
                    <ImageContainer.Main src={data?.attachments?.length > 0 ? ("https://housing-backend-production.up.railway.app/" + data?.attachments?.[0]?.imgPath) : noimg} alt="House" />
                    <ImagesWrapper>
                        {
                            data?.attachments?.slice(1, 5).map((item, index) => {
                                return <ImageContainer.Img key={index} src={"https://housing-backend-production.up.railway.app/" + item?.imgPath} alt="House" />
                            })
                        }
                    </ImagesWrapper>
                </ImageContainer>
                <Section>
                    <div style={{ flex: "3" }}>
                        <Section>
                            <Content>
                                <div className="title">{data.name}</div>
                                <Content.SubTitle>
                                    {data.city},{data.adress},{data.country}
                                </Content.SubTitle>
                            </Content>
                            <Content $row={true}>
                                <Icons.Share /> <div className="info">Share</div>
                                <Icons.Heart onClick={(e) => handleFavourite(e)} $favourite={isFavourite}/> <div className="info">Save</div>
                            </Content>
                        </Section>
                        <Section>
                            <Details>
                                <Icons.Bed />
                                <Details.Title>{data?.houseDetails?.beds || 0} Beds</Details.Title>
                                <Icons.Bath />
                                <Details.Title>{data?.houseDetails?.bath || 0} Baths</Details.Title>
                                <Icons.Garage />
                                <Details.Title>{data?.houseDetails?.garage || 0} Garage</Details.Title>
                                <Icons.Ruler />
                                <Details.Title>{data?.houseDetails?.area || 0} Sq Ft</Details.Title>
                                <Icons.Calendar />
                                <Details.Title>Year Built: {data?.houseDetails?.yearBuilt || 0}</Details.Title>
                            </Details>
                            <Content>
                                <Content $row={true}>
                                    <del>{data?.price > 0 && (data?.price/24).toLocaleString()}/mo</del>
                                    <h2 className="title">{data?.price > 0 && (data?.price/36).toLocaleString()}/mo</h2>
                                </Content>
                                <div style={{ width: "100%", textAlign: "end" }} className="info">{data?.user?.firstname} {data?.user?.lastname}</div>
                            </Content>
                        </Section>
                        <Content>
                            <Content.Title>Description</Content.Title>
                            <Description>{data?.description}</Description>
                        </Content>
                    </div>
                    <Content className='user' $flex="1">
                        <Section style={{ justifyContent: "flex-start" }}>
                            <UserImg src={noimg} />
                            <Content style={{ alignItems: "flex-start" }}>
                                <div className="subTitle">Name</div>
                                <div className="info">Phone number</div>
                            </Content>
                        </Section>
                        <Input placeholder='Name' />
                        <Input placeholder='Phone' />
                        <Input placeholder='Email' />
                        <Input placeholder='Message' />
                        <Checkbox>By submitting this form I agree to Terms of Use</Checkbox>
                        <Button width="100%">Send request</Button>
                    </Content>
                </Section>
                <Map />
            </Container>
            <Recent title="Similar listings" />
        </>
    );
}

export default HouseItem;
