import React, { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import HouseCard from '../HouseCard';
import { Navigation, Pagination } from 'swiper/modules';
import { Arrow, Container, Content } from './style';
import { useNavigate } from 'react-router-dom';

function Recent({title}) {
    const [data, setData] = useState([]);
    const navigate = useNavigate()

    useEffect(() => {
        fetch(`https://housing-backend-production.up.railway.app/api/houses`)
            .then(res => res.json())
            .then(res => {
                setData(res || []);
            });
    }, []);

    useEffect(() => {
        const swiperInstance = document.querySelector('.swiper');
        if (swiperInstance) {
            swiperInstance.swiper.params.navigation.prevEl = '.recent-prev-button';
            swiperInstance.swiper.params.navigation.nextEl = '.recent-next-button';
            swiperInstance.swiper.navigation.init();
            swiperInstance.swiper.navigation.update();
        }
    }, [data]);

    return (
        <Container>
            <Content>
                <h1 className="title">{title? title : "Recent Properties for Rent"}</h1>
                <p className="info" style={{ fontSize: "16px" }}>Nulla quis curabitur velit volutpat auctor bibendum consectetur sit.</p>
            </Content>
            <Swiper
                style={{ height: "500px" }}
                modules={[Navigation, Pagination]}
                navigation={{
                    prevEl: '.recent-prev-button',
                    nextEl: '.recent-next-button',
                }}
                slidesPerView={3}
                spaceBetween={20}
                loop={data.length > 3}
                pagination={{ clickable: true }}
            >
                {data.map((value, index) => (
                    <SwiperSlide key={index}>
                        <HouseCard data={value} onClick={() => navigate(`/properties/${value._id}`)} />
                    </SwiperSlide>
                ))}
            </Swiper>
            <Arrow className="recent-prev-button" $left />
            <Arrow className="recent-next-button" />
        </Container>
    );
}

export default Recent;
