import styled from "styled-components";
import { ReactComponent as arrow } from '../../assets/icons/arrow.svg'

const Container = styled.div`
    position: relative;
    width: 100%;
    max-width: 1440px;
    padding: 96px 130px;
    margin: 0 auto;
`

const Arrow = styled(arrow)`
    position: absolute;
    z-index: 999;
    padding: 15px;
    top: 50%;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    box-shadow: 0px 10px 30px 5px #0D263B1A;
    background-color: white;
    left: ${({ $left }) => ($left && '-25px')};
    right: ${({ $left }) => (!$left && '-25px')};
    transform: rotate(${({ $left }) => (!$left && '180deg')});
    cursor: pointer;
    path {
    fill: black;
    }
`

const Content = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    padding-bottom: 32px;
`


export { Container, Arrow, Content }