import styled from "styled-components";
import { ReactComponent as share } from '../../assets/icons/share.svg'
import { ReactComponent as heart } from '../../assets/icons/heart.svg'
import { ReactComponent as bed } from '../../assets/icons/bed.svg'
import { ReactComponent as bath } from '../../assets/icons/bath.svg'
import { ReactComponent as car } from '../../assets/icons/car.svg'
import { ReactComponent as ruler } from '../../assets/icons/ruler.svg'
import { ReactComponent as calendar } from '../../assets/icons/calendar.svg'


const Container = styled.div`
    width: 100%;
    max-width: var(--width);
    padding: var(--padding);
    margin: 0 auto;
    display: flex;
    flex-direction: column;   
`

const Details = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 32px 0;
`

Details.Title = styled.div`
    margin: 0 24px 0 8px;
    font-family: Montserrat;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0%;
    color: #696969;
`

const Section = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
`

const Content = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: ${({ $row }) => (!$row && 'column')};
    flex: ${({ $flex }) => $flex || 'initial'};

    &.user {
    padding: 20px;
    border-radius: 3px;
    margin-left: 20px;
    border: 1px solid #E6E9EC;
    gap: 24px;
    }
`

Content.Title = styled.div`
    width: 100%;
    font-weight: 600;
    font-size: ${({ large }) => (large ? '24px' : '18px')};
    color: #0d263b;
`

Content.SubTitle = styled.div`
    width: 100%;
    margin-top: 8px;
    font-weight: 400;
    font-size: 16px;
    color: #696969;
`

const Icons = styled.div``

Icons.Bed = styled(bed)``
Icons.Bath = styled(bath)``
Icons.Garage = styled(car)``
Icons.Ruler = styled(ruler)``
Icons.Calendar = styled(calendar)``

Icons.Share = styled(share)`
width: 35px;
height: 35px;
border-radius: 10px;
padding: 4px;
cursor: pointer;
    &:active {
        transform: scale(0.9);
    }
    margin: 0 10px 0 26px;
`
Icons.Heart = styled(heart)`
width: 35px;
height: 35px;
border-radius: 50%;
padding: 4px;
cursor: pointer;
    &:active {
        transform: scale(0.9);
    }
    margin: 0 10px 0 26px;
    background-color: ${({ $favourite }) => ($favourite ? "#CC5040" : "white")};
    & path {
        fill: ${({ $favourite }) => ($favourite && "white")};
    }
`

const Description = styled.div`
    width: 100%;
    margin-top: 16px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
`

const UserImg = styled.img`
    width: 56px;
    height: 56px;
    border-radius: 50%;
    margin-right: 8px;
`

const ImageContainer = styled.div`
    display: flex;
    margin: 24px 0;
`

ImageContainer.Main = styled.img`
    width: 580px;
    height: 400px;
`

ImageContainer.Img = styled.img`
    width: 280px;
    height: 190px;
`

const ImagesWrapper = styled.div`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-left: 20px;
`

export { Container, Section, Content, Icons, Details, Description, UserImg, ImageContainer, ImagesWrapper }